  box-shadow: inset 0px 4px 15px var(--colorBlackOpacity);
}

textarea{
	width: 100%;
  padding: 10px;
	border-radius: 5px;
	background-color: rgba(214, 214, 214, 0.2);
	background-color: var(--colorGray_20);
  box-shadow: inset 0px 4px 15px #00000096;
  box-shadow: inset 0px 4px 15px var(--colorBlackOpacity);
	border: transparent;
	resize: none;
}

textarea {
  width: 100%;
  padding: 10px;
  border-radius: 5px;
  background-color: rgba(214, 214, 214, 0.2);
  background-color: var(--colorGray_20);
  box-shadow: inset 0px 4px 15px rgba(0, 0, 0, 0.5);
  border: transparent;
  resize: none;
}

:root {
  --curve: 18;
  --colorBackground: #E5E5E5;
  --colorWhite: #FEFEFE;
  --colorGreenDark: #1A2818;
  --colorBlack: #010101;
  --colorBlackOpacity: #00000096;
  --colorGray: #D7D7D7;
  --colorGray_20: rgba(214, 214, 214, 0.2);
  --colorGreenLight: #92C149;
  --colorGreenLight_31: rgba(145, 193, 73, 0.31);
  --colorRed: #ff2a2a;
  --colorYellow: #ffd808;
  --colorYellow_65: rgba(255, 204, 2, 0.65);
  --colorYellowOpacity: #ffda0a98;
}

/* ===== Scrollbar CSS ===== */
/* Firefox */
* {
  scrollbar-width: auto;
  scrollbar-color: #ffd808 #010101;
  scrollbar-color: var(--colorYellow) var(--colorBlack);
}

/* Chrome, Edge, and Safari */
*::-webkit-scrollbar {
  width: 8px;
  overflow-x:hidden;
}

*::-webkit-scrollbar-track {
  background: #010101;
  background: var(--colorBlack);
}

*::-webkit-scrollbar-thumb {
  background-color: #ffd808;
  background-color: var(--colorYellow);
  border-radius: 10px;
  border: 8px solid rgba(255, 204, 2, 0.65);
  border: 8px solid var(--colorYellow_65);
}
/* ------------- LOGIN--------------- */
.container-login {
  align-items: center;
  background-color: var(--colorWhite)fff;
  display: flex;
  justify-content: center;
  padding: 20px;
  width: 100%;
}
.login-form-content {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
.login-form-content * {
  padding: 0;

  margin: 0;
}
.login-page {
  background-color: var(--colorGray);
  border-radius: 14px;
  box-shadow: 0.4rem 0.4rem 0 0 var(--colorBlack);
  height: 80vh;
  max-width: 400px;
  padding: 25px;
  width: 100%;
}
.form {
  margin-top: 50px;
}
.logo {
  display: block;
  margin: auto;
  width: 80%;
}
.button-login {
  background-color: var(--colorBlack);
  border-radius: 10px;
  border: none;
  color: #e3e3e3;
  cursor: pointer;
  display: block;
  font-weight: bold;
  margin: 45px auto;
  padding: 10px;
  text-transform: uppercase;
  width: 60%;
}
.button-login:hover {
  background-color: var(--colorYellow);
  color: var(--colorBlack);
  transition: 0.3s;
}
.login-page p {
  cursor: pointer;
  font-size: 14px;
  font-weight: 600;
  margin: 15px 0;
}
.login-page a {
  color: var(--colorBlack);
  font-size: 14px;
  font-weight: 600;
  padding: 5px;
}
.login-page a:hover {
  background-color: var(--colorYellow);
  padding: 5px;
}
